//наследуемый класс. Перезаписываемые методы close и setEventListeners.
import Popup from './Popup.js'

export default class PopupWithForm extends Popup {

    constructor(popupSelector, {handleSubmitForm}) {
        super(popupSelector)

        this._form = this._popup.querySelector('.form')
        this._handleSubmitForm = handleSubmitForm
        this._inputList = this._form.querySelectorAll('.popup__form')
    }

    //получает значения полей формы и возвращает объект с их значениями
    _getInputValues() {
        this._valuesForInputs = {}

        this._inputList.forEach(input => {
            this._valuesForInputs[input.getAttribute('name')] = input.value
        })

        return this._valuesForInputs
    }

    setEventListeners() {
        super.setEventListeners()

        this._form.addEventListener('submit', evt => {
            evt.preventDefault()
            this._handleSubmitForm(this._getInputValues())
        })
    }

    addCurrentUserData(userData) {
        this._inputList.forEach(input => {
            if (userData[input.name] !== undefined) {
                input.value = userData[input.name]
            } else {
                input.value = ''
            }
        })
    }
    // //можно записать так addCurrentUserData в компактном виде
    // addCurrentUserData(userData) {
    //     this._inputList.forEach(input => { 
    //         input.value = userData[input.name] || ''
    //      })
    // }

    close() {
        super.close()
        this._form.reset()
    }

    //изменение текста на кнопке сабминта формы
    setSubmitButtonCaption(caption) {

    }

    //изменение состояния кнопки сабмита формы
    setButtonState(isLoading) {

    }

    setAvatarLink(link) {
        const image = document.querySelector('.profile__avatar')
        image.src = link.avatar
        console.log(link)
    }
}